@use "sass:math";



@import './styles/_variables.scss';
@import './styles/_mixins';
@import './styles/apropos.scss';
@import './styles/banner.scss';
@import './styles/card.scss';
@import './styles/cardlist.scss';
@import './styles/home.scss';
@import './styles/pagenotfound.scss';
@import './styles/property.scss';
@import './styles/slideshow.scss';
@import './styles/tags.scss';




* {
    box-sizing: border-box;
  }


 html,  .app {
 
    height: 100%;
    margin: 0;
    padding: 0;
}

  body {  
    // border: dashed orange 2px;
    margin: 0;
    padding: 0;
    width: 100vw;
    max-width: 1440px; 
    min-height:100vh;
    
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    margin:auto;
   overflow-x: hidden;
  }


  #root {
  // border: solid yellow 3px;
  
  margin-left: auto; 
  margin-right: auto; 
     
  }

  .App{
    // border: solid green 2px;
    display: flex; 
    flex-direction: column;
    gap:50px;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    padding-top: 45px;
  }

  // w> Recommendation: math.div($header-height-d, $desktop-height) or calc($header-height-d / $desktop-height)

  .header{
    height: math.div($header-height-d,$desktop-height) *100%;
    width:  math.div($body-width-d,$desktop-width)*100%;;
    display: flex;
    justify-content: space-between ;
    img{
      height: math.div($logo-height-d, $header-height-d)*100%;
      width: math.div($logo-width-d,$body-width-d)*100%;
    }
  }


.header_nav{
    // border: solid blue 1px;
    width:math.div($nav-width-d,$desktop-width) *100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.footer{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $black;
  color: #fff;
  height: 209px;
  margin-top: auto;
  width: 100%;

  .logocontainer{
    display: flex;
    flex-direction: column;
    align-items: center;

  }

 .Flogo{
  color:#fff;
 }
    
}

.fallback{
  color: #FF6060;
  font-family: Montserrat;
  font-size: 36px;
  font-style: normal;
  font-weight: 500;
  line-height: 142.6%;
}



// @media #{$bp-mobile} {
  
// }






@media (max-width: 768px) {
  .header {
    justify-content: space-between;
    padding: 10px;

    img {
        margin-right: 10px;
        width: 145px;
height: 46.881px;
flex-shrink: 0;
    }

    .header_nav {
        flex-direction: row;
        gap: 10px;
        width: auto;

        a {
            font-size: 14px;
            padding: 10px 0;
            text-align: center;
        }
    }
}
  .footer {
   
      flex-direction: column;

      padding: 20px;    
  }

  .logocontainer{
    width:50%;

    .kasa{
    
      text-align: center;
      width:75%;

      
      
    }
  }
}














